import appRichText from "../../../components/basic-component/app-rich/parse.vue";

export default {
    data() {
        return {
            order_by: 'bonus_price desc',
            captain_id: null,
            keyword: '',
            page: 2,
            setting: '',
            more_list: false,
            toSearch: false,
            list: []
        }
    },
    components: {
        "app-rich-text": appRichText
    },
    methods: {
        toOrder(nickname) {
            uni.navigateTo({
                url: '/plugins/bonus/order/order?nickname=' + nickname
            }); 
        },

        cancelSeacrch() {
            this.keyword = '';
            this.toSearch = !this.toSearch;
            this.page = 2;
            this.getList();
        },

        beSearch() {
            this.toSearch = !this.toSearch;
            this.page = 2;
        },

        goSearch() {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },

        toggle() {
            if (this.order_by == 'bonus_price desc') {
                this.order_by = 'bonus_price'
            } else if (this.order_by == 'bonus_price') {
                this.order_by = 'bonus_price desc'
            }
            this.list = [];
            this.page = 2;
            this.getList();
        },

        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.bonus.setting,
            }).then(response=>{
                that.getList();
                if(response.code == 0) {
                    that.setting = response.data.list;
                    if (that.setting.form && that.setting.form.members) {
                        uni.setNavigationBarTitle({
                            title: that.setting.form.members,
                        })
                    } else {
                        uni.setNavigationBarTitle({
                            title: '队员',
                        })
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
            });
        },

        getList() {
            let that = this;
            that.$request({
                url: that.$api.bonus.team,
                data: {
                    keyword: that.keyword,
                    keyword_1: 1,
                    order_by: that.order_by,
                },
                method: 'post'
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },

        getMore() {
            let that = this;
            that.$request({
                url: that.$api.bonus.team,
                data: {
                    keyword: that.keyword,
                    keyword_1: 1,
                    order_by: that.order_by,
                    page: that.page
                }
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.getSetting();
    },
    onReachBottom() {
        this.getMore();
    }
}
